<template>
	<!--收货地址-->
	<div class="shouhuodizhi">
		<!--收货地址-->
		<div class="shouhuodz">
			<div @click="backGo"><img src="/img/wode/shezhi/shezhi-back.png" alt="" /></div>
			<div>收货地址</div>
			<div @click="addDizhi">新增</div>
		</div>
		<!--全部收货地址-->
		<div class="shdz-all">
			<ul>
				<li v-for="(item,index) in shdzData" class="item">
					<div>
						<div class="shrxx">
							<div>{{ item.shr }}</div>
							<div>{{ item.phone }}</div>
						</div>
						<p class="shrdz">{{ item.diqu }}{{ item.jiedao }}{{ item.xxdz }}</p>
						<hr />
						<div class="chaozuo">
							<div class="left">
								<input type="radio" name="check" class="inp" />
								<span>默认地址</span>
							</div>
							<div class="right">
								<div class="bianji" @click="bianji(item.id)">
									<img src="/img/wode/shezhi/dizhi-bianji.png" alt="" />
									<span>编辑</span>
								</div>
								<div class="delete">
									<img src="/img/wode/shezhi/dizhi-shanchu.png" alt="" />
									<span>删除</span>
								</div>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'shouhuodizhi',
		data: function() {
			return {
				// 收货地址数据
				shdzData: [],
				checked: true
			}
		},
		methods: {
			//编辑
			bianji(id){
				this.$router.push({
					name: "bjdz",
					params:{
						idData:id
					}
				})
			},
			// 删除
			deleted(){
				//在数据库中根据id删除项目
			},
			// 新增
			addDizhi(){
				this.$router.push('/xzdz')
			},
			backGo(){
				this.$router.go(-1)
			}
		},
		mounted() {
			this.$axios.get('/json/shouhuodizhi.json').then(res => {
//				console.log(res.data)
				this.shdzData = res.data
			}).catch(err => {
				console.log(err)
			})
		}
	}
</script>

<style scoped>
	.shouhuodizhi {
		width: 100%;
		height: 100%;
		background-color: #cccccc;
	}
	/*收货地址*/
	
	.shouhuodizhi .shouhuodz {
		width: 100%;
		padding: 80px 30px 30px;
		font-size: 22px;
		color: #333333;
		background-color: white;
		font-weight: bold;
		display: flex;
		justify-content: space-between;
	}
	
	.shouhuodizhi .shdz-all ul .item {
		width: 100%;
		height: 282px;
		padding: 30px 30px;
		background-color: white;
		font-size: 20px;
		line-height: 30px;
		margin: 30px 0 30px 0;
	}
	/*收货人信息*/
	
	.shouhuodizhi .item .shrxx {
		display: flex;
		justify-content: space-between;
		margin-bottom: 40px;
	}
	/*地址*/
	
	.shouhuodizhi .item .shrdz {
		margin-bottom: 30px;
	}
	
	.shouhuodizhi hr {}
	/*地址操作*/
	
	.shouhuodizhi .item .chaozuo {
		display: flex;
		justify-content: space-between;
	}
	
	.shouhuodizhi .item .chaozuo .left .inp {
		margin: 0 20px 0 0;
		display: inline-block;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		line-height: 40px;
	}
	
	.shouhuodizhi .item .chaozuo .right {
		width: 30%;
		display: flex;
		justify-content: space-between;
	}
</style>